.theme {
    position:fixed;
    display:flex;
    align-items:center;
    z-index: 2;
    border-radius:50%;
    background-color:transparent;
    padding:20px 20px;
    top:100px;
    right:35px;
  }
    .themetwo{
    position:fixed;
    display:flex;
    align-items:center;
    z-index: 2;
    border-radius:50%;
    background-color:transparent;
    padding:20px 20px;
    top:160px;
    right:42px;
  }
  
  .theme-logo {
    /* pointer-events: none; */
    z-index:1 ;
    position: absolute;
    
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .theme-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .theme-header {
  
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
   
    color: #4b8118;
  }
  
  .theme-link {
    color:#4b8118;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
