#about{
        padding: 90px 0px;
        display: flex;
        flex-direction: column;
        /* color: rgb(52, 51, 51); */
} 
#aboutsec{
    display:flex;
    justify-content: space-around;
}
#aboutme{
    font-size:29px;
    
    padding: 5px 15px;
    border-radius: 15px;
 
    margin-bottom: 30px;
}
.paragraph{
   
    font-size: 24px;
}
.home-img{
    
    height: 380px;
    width: 360px;
    margin: auto;
   
}
#user-detail-intro{
    color: transparent;
}
@media screen and (min-width:658px) and (max-width:1358px){
    #aboutsec{
        display:flex;
        justify-content: space-around;
        flex-direction: column;
        gap:30px;
    }
    .home-img{
        border-radius:50% ;
        height: 330px;
        width: 280px;
        margin: auto;
        margin-top: 30px;
        box-shadow: #4b8118 0px 0px 0px 10px;
    }
    #aboutme{
        display: none;
    }
    .paragraph{
        text-align: justify,center;
        font-size: 24px;
        padding: 0px 10px;
    }
    #summary{
        margin-top: 30px;
    }
}
@media screen and (max-width:658px){
    #aboutsec{
        display:flex;
        justify-content: space-around;
        flex-direction: column;
        gap:30px;
    }
    .home-img{
        border-radius:50% ;
        height: 310px;
        width: 280px;
        margin: auto;
        margin-top: 30px;
        /* box-shadow: #4b8118 0px 0px 0px 10px; */
    }
    #aboutme{
        display: none;
    }
    .paragraph{
        text-align: justify,center;
        font-size: 15px;
        padding: 0px 10px;
    }
    #summary{
        margin-top: 30px;
    }
}