#projects{
    
        padding:0px 10px;
        width:100%;
        display: flex;
        justify-content: center;
        align-items:center;
        flex-direction: column;
        
}
#projecthead{
    font-size:37px;
    font-family: serif;

     color:white;
     border-radius:15px;
     padding:0px 25px;
     padding-top: 0px;
     margin-top: 90px;
     margin-bottom: 70px;
}


.project-card{
    display:flex;
    padding:10px 10px;
    margin-bottom: 40px;
    border-radius: 15px;
    box-shadow: #4b8118 5px 3px 15px;
    
}
#imgdiv{
    width:50%;
    padding:10px 5px;
}
#imgdiv img{
    display: block;
    border-radius: 10px;
    width:100%;
    height: 100%;
}

#imgdiv:hover img {
    cursor: pointer;
    transform: scale(1.1);
}
#imgdiv img {
    display: block;
    transition: transform 3s;
}
#imgdivimg {
    display: block;
    width: 100%;
}
img {
    vertical-align: middle;
}
user agent stylesheet
img {
    overflow-clip-margin: content-box;
    overflow: clip;
}
#descriptiondiv{
    width:50%;
    text-align: left;
    display:flex;
    flex-direction: column;
    align-items: center;
    padding:0% 3%;
}
.livebutton{
    padding:2px 20px;
    background-color:#4b8118;
    border-radius: 15px;
    color: white;
    font-family: serif;
    display:flex;
    gap:8px;

}
.project-tech-stack{
    display: flex;
    justify-content: left;
    font-family: serif;
    align-items: center;
    padding:3% 0%;
    gap:5px;
}
.project-title{
    font-size:23px;  
}
#linkcontainer{
    display:flex;
    gap:5px;
}
.link:hover{
    text-decoration: none;
    transform: scale(0.9);
}
#texttackdiv{
    display: grid;
    grid-template-columns: repeat(6,1fr);
}

@media screen and (max-width:1145px){
    .project-card{
        display:flex;
        flex-direction: column;
        padding:20px 10px;
        margin-bottom: 40px;
        border-radius: 15px;
        box-shadow: #4b8118 5px 3px 15px;
        
    }
    #texttackdiv{
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
    #imgdiv{
        width:100%;
        padding:10px 5px;
    }
    #descriptiondiv{
        width:100%;
        text-align: left;
        display:flex;
        flex-direction: column;
        align-items: center;
        padding:0% 3%;
    }
    .link:hover{
        text-decoration: none;
        transform: scale(0.9);
    }
    #linkcontainer{
        display:flex;
        gap:50px;
    }
}