        padding:7% 4%;
        display:flex;
        flex-direction: column;
        gap:'20px';
        justify-content: space-evenly;
        align-items: center;
        text-align: center;
    }
    .linkicon{
        width: 80px;
        border-radius: 50%;
    }
   
    #mobilediv{
        display: flex;
    }
    #typestyle{
        width: 100%;
    }
    #animationbox{
        margin: 100px 0px;
