#star-language{
    display:flex;
    justify-content:space-evenly;
    margin-top: 50px;
    align-items: center;
    width: 100%;
}
#stat-container{
    margin-bottom:50px;
    margin-top:20px;
    padding-top: 100px;
}
#headbutton{
    background-color:#4b8118;
    padding: 3px 0px;
    border-radius: 15px;
}
#heading{
    font-size:39px;
    color:white;
    padding:0px 15px;
    font-family: serif;
}
#github-stats-card{
   
    border: 1px solid white;
    border-radius: 10px;
}
#github-top-langs{
    
    border: 1px solid white;
    border-radius: 10px;
}

@media screen and (max-width:1145px){
    #star-language{
        display:flex;
        justify-content:space-evenly;
        flex-direction: column;
        gap: 50px;
        margin-top: 50px;
        align-items: center;
        width: 100%;
    }
    #github-streak-stats{
        width:60%;
    }
    #github-stats-card{
        width:60%;
        border: 1px solid white;
        border-radius: 10px;
    }
    #github-top-langs{
        width: 60%;
        border: 1px solid white;
        border-radius: 10px;
    }
}
